<template>
    <div class="liked">
        <div class="more clearfix">
            <div class="moreList fl" v-for="(item,index) in moreData" :key="index" @click="goDetail(item.goods_id)">
                <img v-lazy="item.goods_big_logo" />
                <ul>
                    <li>{{item.goods_name}}</li>
                    <li>￥{{item.goods_price}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import "../assets/css/liked.less";
    export default {
        data(){
            return{
                moreData:[]   
            }
        },
        created(){
            this.cid = Math.ceil(Math.random()*1481+1)
            this.List(this.cid);
        },
        methods:{
            List(cid) {
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: 'GET',
                    url: '/goods/search',
                    params: {
                        cid,
                    }
                }).then(result => {
                    this.$toast.clear();
                    if (result.data.meta.status == 200) {
                        
                        this.moreData = result.data.message.goods;    
                    }
                }).catch(err => {
                    this.$toast.clear();       
                })
            },
            Detail(goods_id) {
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: 'GET',
                    url: '/goods/detail',
                    params: {
                        goods_id,
                    }
                }).then(result => {
                    this.$toast.clear();
                    if (result.data.meta.status == 200) {
                        
                    }
                }).catch(err => {
                    this.$toast.clear();       
                })
            },
            goDetail(goods_id) {
                this.$router.push({name: 'Detail', query: {goods_id}});
                // this.$router.go(this.Detail);
                // this.Detail(goods_id);
            }
        }
    }
</script>

