<template>
    <div class="home">
        <van-nav-bar>
            <template #title>
              <img src="https://image3.suning.cn/uimg/cms/img/159706790064259413.gif">
            </template>
            <template #right>
              <img src="https://image1.suning.cn/uimg/cms/img/157199321817918653.png">
            </template>
            <template #left>
              <img src="https://image1.suning.cn/uimg/cms/img/157199320847433454.png"  @click="goPage('Classification')">
            </template>
        </van-nav-bar>
        <van-search
          shape="round"
          background="rgb(255,219,71)"
          placeholder="商品/店铺"
          v-model="value"
          @click="goPage('GoSearch')"
        />
        <div class="shuffling">
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                <van-swipe-item v-for="(item,index) in bannerData" :key="index">
                    <img class="auto-img" v-lazy="item.image_src" alt />
                </van-swipe-item>
            </van-swipe>
        </div>
        <div class="catitems">
            <ul>
                <li v-for="(item,index) in catitemsData" :key="index">
                    <img class="auto-img" v-lazy="item.image_src" alt />
                </li>
            </ul>
        </div>
            <ul v-for="(item,index) in floorData" :key="index" class="floor">
                <li>
                    <img class="auto-img" v-lazy="item.floor_title.image_src" alt />
                    <img  v-for="(item2,index2) in item.product_list" :key="index2" class="auto-img smallImg" v-lazy="item2.image_src" alt />
                </li>
            </ul>
        <img class="hope" src="../../assets/images/hope.webp" alt>
        <liked></liked>
    </div>
</template>

<script>
    import liked from "../../components/Liked";
    import "../../assets/css/home.less";
    export default {
        components:{
            liked
        },
        data(){
            return{
                value:"",
                bannerData:[],
                catitemsData:[],
                floorData:[],
            }
        },
        created(){
            this.Banner();
            this.Catitems();
            this.Floor();
        },

        methods:{
            goPage(name) {
                this.$router.push({name});
            },
            Banner() {
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: 'GET',
                    url: '/home/swiperdata',
                }).then(result => {
                    this.$toast.clear();
                    if (result.data.meta.status == 200) {
                        this.bannerData = result.data.message;                           
                    }
                }).catch(err => {
                    this.$toast.clear();       
                })
            },
            Catitems() {
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: 'GET',
                    url: '/home/catitems',
                }).then(result => {
                    this.$toast.clear();
                    if (result.data.meta.status == 200) {
                        this.catitemsData = result.data.message;                           
                    }
                }).catch(err => {
                    this.$toast.clear();       
                })
            },
            Floor(){
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: 'GET',
                    url: '/home/floordata',
                }).then(result => {
                    this.$toast.clear();
                    if (result.data.meta.status == 200) {
                        
                        this.floorData = result.data.message;
                    }
                }).catch(err => {
                    this.$toast.clear();       
                })
            }
        }
    }
</script>

<style lang="less" scoped>
</style>